	export default {
		data() {
			return {
				style: {
					pageHeight: 0,
					contentViewHeight: 0
				},
                code: '',
                disabled: false,
                placeholder: '填写您的好友邀请码',
                codeToken: ''
			}
		},
        onLoad() {
            const info = uni.getSystemInfoSync();
            this.style.pageHeight = info.windowHeight;
            this.style.contentViewHeight = info.windowHeight; //像素
        },
        onNavigationBarButtonTap(){
            if (!this.$store.state.isLogin) {
                uni.navigateTo({
                    url: '/pages/login/login',
                    animationType: 'slide-in-bottom'
                });
                return
            }
            this.codeToken = this.$store.state.userInfo.loginToken;
            uni.request({
                url: this.websiteUrl + '/api/shareupd/code/' + this.code,
                method: 'POST',
                header: {
                    Authorization: this.codeToken
                },
                data: {},
                success: res => {
                    if (res.data.status === 0) {
                        this.disabled = true;
                        this.code = '';
                        this.placeholder = '您已填写，邀请码只能填写一次'
                    } else if (res.data.status == 1001) {
                        uni.showToast({
                            title: res.data.message,
                            icon: 'none',
                            mask: true
                        });
                        setTimeout(function () {
                            uni.hideToast();
                            uni.navigateTo({
                                url:"/pages/login/login",
                                animationType:"slide-in-right"
                            });
                        }, 300)
                    }
                },
                fail: () => {},
                complete: () => {}
            });
        }
	}
